import React, { memo } from 'react';
import ListItem from './ListItem';
import SectionHeader from './SectionHeader';
import LinkUrl from './LinkUrl';
import { Tooltip } from 'react-tooltip';


const Publication = memo(({ title, url, authors, year, journal }) => (
    <div className="publication" data-tooltip-content={`${year}, ${journal}`} data-tooltip-id='meta'>
        <ul>
            <ListItem text={<LinkUrl text={title} url={url} />} />
        </ul>
        <Tooltip id='meta' place='top' style={{ backgroundColor: "#6C63FF" }} />
    </div>
));




const Experience = () => {
    const skills = [
        {
            category: "Software Development and Coding",
            skills: "C#, C++, Python, JavaScript, TypeScript, React, HTML/CSS, Flask, SocketIO, GitHub, GitLab, Yarn, npm, NGINX, RabbitMQ, Celery, Processing, Visual Studio Code, Front end web development"
        },
        {
            category: "Digital Media, Graphics, Design, and Visualization",
            skills: "3D Graphics programming, Shader programming, HLSL, GLSL, OpenGL, WebGL, Game Engines, Unity, Unreal, D3.js, Three.js, Figma, Adobe Creative Suite, Blender, 3D Modeling, 3D Printing, 3D Scanning, Audio Production, Audacity, Logic Pro, Ableton, Reaper"
        },
        {
            category: "Data Science and AI",
            skills: "R, Python, NumPy, Pandas, Scikit-learn, Matlab, SQL, HDF5, Machine learning, decision trees, regression, Anaconda, Torch, TensorFlow, PyTorch, Keras, natural language processing (NLP), computer vision, image processing, neural networks, deep learning, LLM, Diffusion"
        },
        {
            category: "Cloud Computing",
            skills: "AWS (S3 & EC2), Jetstream2, Nautilus, Kubernetes, Docker, Virtual Machines, Ubuntu"
        }
    ];

    const renderJobItem = (title, subtitle, start, end, skills) => (


        <div data-tooltip-html={
            `${start} - ${end} <div class="work-xp-tooltip"> ${skills.map(skill => `
            <div class="skill"> ${skill.trim()} </div>`).join('')}</div>`
        }
            data-tooltip-id='details'>
            <ListItem text={
                <div>
                    <div className="job-title">
                        <b>{title}</b>
                    </div>
                    <div className="company-name">
                        {subtitle}
                    </div>
                </div>}
            />
            <Tooltip id='details' place='top'
                style={{ backgroundColor: "#6C63FF" }} />


        </div>
    );

    return (
        <>
            <section id="experience">
                <div className="experience-container">
                    <div className="main-container main-experience-container">
                        <div className="experience-column publications">
                            <SectionHeader text={"Publications"} iconName={"publication_icon"} />
                            <Publication title={"Interactive Analysis Tools for Visualizing the Universe"}
                                url={"https://www.proquest.com/openview/68f7fe43529067a68fe04903f9714252/1?pq-origsite=gscholar&cbl=18750&diss=y"}
                                year={"2023"}
                                journal={"University of California, Santa Cruz, ProQuest Dissertations Publishing"} />

                            <Publication title={"CosmoVis: An Interactive Visual Analysis Tool for Exploring Hydrodynamic Cosmological Simulations"}
                                url={"https://ieeexplore.ieee.org/document/9736650"}
                                year={"2022"}
                                journal={"IEEE Transactions on Visualization and Computer Graphics"} />

                            <Publication title={"Volumetric Reconstruction for Interactive Analysis of the Cosmic Web"}
                                url={"https://www.sci.utah.edu/~beiwang/visastro2020/VisAstro2020-CosmicWeb.pdf"}
                                year={"2020"}
                                journal={"IEEE VIS"} />

                            <Publication title={"RuleVis: Constructing Patterns and Rules for Rule-Based Models"}
                                url={"https://ieeexplore.ieee.org/document/8933596"}
                                year={"2019"}
                                journal={"IEEE VIS"} />

                            <Publication title={"IGM‐Vis: Analyzing Intergalactic and Circumgalactic Medium Absorption Using Quasar Sightlines in a Cosmic Web Context"}
                                url={"https://arxiv.org/abs/1812.07092"}
                                year={"2019"}
                                journal={"Eurographics European Association for Computer Graphics EuroVis"} />
                        </div>

                        <div className='experience-right-column'>

                            <div className="experience-column work-experience">
                                <SectionHeader text={"Work Experience"} iconName={"work_icon"} />
                                <ul>
                                    {renderJobItem("Technical Artist", "Liminal Space Collective", "Jul 2023", "Present", ["Electronics", "Lighting", "3D Scanning", "Object Reconstruction", "3D Modeling", "Blender", "Budgeting", "Carpentry"])}
                                    {renderJobItem("Full-stack 3D Real-time Rendering Graphics Developer", "Ph.D. Student Researcher, Creative Coding Lab, UCSC", "Oct 2018", "Jun 2023", ["Data Vis", "Simulations", "3D Graphics", "Research", "UI", "3D Animation", "HTML", "CSS", "Javascript", "THREE", "GLSL", "D3", "Python", "Figma", "Journal Publications", "Conference Presentations"])}
                                    {renderJobItem("Research Mentor", "Polygence", "May 2022", "Jun 2023", ["Mentoring", "Computer Vision", "Research", "Technical Writing"])}
                                    {renderJobItem("Teaching Assistant", "Computation Media Department, UCSC", "Sep 2018", "Jun 2023", ["Unity", "OpenGL", "HLSL", "GLSL", "C#", "C++", "Python", "Computer Graphics", "Game Engines", "Digital Audio", "Musical Data"])}
                                    {renderJobItem("Game Technologies Class Instructor", "Computation Media Department, UCSC", "Sep 2022", "Dec 2022", ["Unity", "C#", "Blender", "GitHub", "Canvas", "3D Graphics"])}
                                    {renderJobItem("Data Visualization Intern", "Lab En Flujo, Universidad de los Andes", "Aug 2021", "Aug 2022", ["Data Vis", "D3", "Javascript", "Python", "NLP", "HTML", "CSS", "React"])}
                                    {renderJobItem("Data Visualization Computer Science Lead Intern", "NASA JPL, Caltech, and ArtCenter", "Jun 2019", "Aug 2019", ["Data Vis", "Simulations", "3D Graphics", "Javascript", "THREE", "HTML", "CSS"])}
                                    {renderJobItem("Jr. Data Analyst", "Tempus Labs", "Oct 2017", "Aug 2018", ["R", "Python", "Clinical Oncology Data"])}
                                    {renderJobItem("Technical Artistic Associate", "Shattered Globe Theatre Company", "Aug 2013", "Aug 2018", ["2D/3D Scenic Design", "AutoCAD", "Lighting", "Audio", "Electronics", "Carpentry", "Fundraising"])}
                                    {renderJobItem("Weather Balloon Altitude Control Engineer", "Adler Planetarium", "Jun 2016", "Sep 2016", ["Electronics", "Sensors", "3D Modeling", "3D Printing", "MatLab", "Field Tests"])}
                                </ul>
                            </div>

                            <div className="experience-column skills-technologies">
                                <SectionHeader text={"Skills and Technologies"} iconName={"wrench_icon"} />
                                {skills.map((skillGroup, index) => (
                                    <div key={index} className="skill-group">
                                        <ul>
                                            <ListItem text={skillGroup.category} />
                                        </ul>
                                        <div className="skills-container">
                                            {skillGroup.skills.split(',').map((skill, idx) => (
                                                <div key={idx} className="skill">
                                                    {skill.trim()}
                                                </div>
                                            ))}
                                        </div>
                                    </div>
                                ))}
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        </>
    );
}

export default Experience;
